<!-- 这个登录组件如果成功，则跳转到 mdEditor组件上去(通过 this.$router.push方法），如果失败，
    则提示错误信息，用户叉掉，或者点击cancel都可以让这个组件消失。 -->
<template>
  <div>
    <!-- 如果登录成功，则隐藏此组件 -->
    <a-modal
      title="请登录"
      :visible="visible"
      :confirm-loading="confirmLoading"
      width="400px"
      @ok="handleOk"
      cancelText="取消"
      @cancel="handleCancel"
      okText="登录"
    >
      <!-- <p>{{ ModalText }}</p> -->
      <div style="text-align: center">
        账号：<input type="text" v-model="account" />
      </div>
      <br />
      <div style="text-align: center">
        密码：<input type="text" v-model="password" />
      </div>
      <template slot="footer">
        <a-button @click="handleCancel" style="margin-right: 60px"
          >关闭</a-button
        >
        <a-button @click="handleOk" style="margin-right: 80px">登录</a-button>
      </template>
    </a-modal>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "myLoginComponent",
  data() {
    return {
      // ModalText: "这是一个登录表单",
      // visible: this.$store.state.user.showLogin,
      confirmLoading: false,
      account: "",
      password: "",
    };
  },
  computed: {
    //不是这样获取store中的数据的吗？嗯，是这样。
    visible: {
      get() {
        return this.$store.state.user.showLogin;
      },
      set(val) {
        this.setLogin(val);
      },
    },
  },
  methods: {
    ...mapActions(["setLogin"]),
    handleOk(e) {
      //在这里判断登录信息，通过则让此组件隐藏，并且跳转路由。
      // this.confirmLoading = true;//？？？

      if (this.account == "123456" && this.password == "123456") {
        this.visible = false;
        this.confirmLoading = false;
        this.$store.dispatch("isLogin", true);
        this.$router.replace({
          path: "/mdEditor",
        });
      }
    },
    handleCancel(e) {
      this.visible = false;
    },
  },
};
</script>

