import Vue from "vue";
import Router from 'vue-router'
Vue.use(Router)

const routes = [
    {
        path: '/',
        name: 'homeComponent',
        component: () => import('../components/home.vue'),
        children: [
            {
                path: 'cate',
                name: 'articleListCate',
                component: () => import('../components/articleListCate'),
                // props($route){
                //     return{
                //         name:$route.query.name,
                //     }
                // }
            },
            {
                path: '',
                name: 'articleListNew',
                component: () => import('../components/articleListNew'),
            },
        ]

    },
    {
        path: '/mdEditor',
        name: 'mdEditorComponent',
        component: () => import('../components/mdEditor.vue'),
        meta: {
            requireAuth: true // 编辑板 需要验证是否登录
        }
    },
    {
        path: '/articleView/:id/:name',
        name: 'articleViewComponent',
        props($route) {
            return {
                id: $route.params.id,
                name: $route.params.name,
            }
        },
        component: () => import('../components/articleView.vue')
    }

]
const route = new Router({
    routes
})

export default route;